import React from 'react'
import { motion } from 'framer-motion'
import { Link } from 'react-router-dom'
import bg1 from '../assets/bg1.png'
import bg2 from '../assets/bg2.png'

const list = [
    {id:1, name:'پروژه های تحقیقاتی', link:'/ResearchProj', img:bg1},
    {id:2, name:'پروژه های اجرایی', link:'/ImplementProj', img:bg2},
    {id:3, name:'مقالات', link:'Articles', img:bg2},
    {id:4, name:'درباره من', link:'AboutMe', img:bg2},
    {id:5, name:'کتاب ها', link:'Books', img:bg2},
    {id:6, name:'فیلم ها', link:'Media', img:bg2},
]

function Cards() {

    const variants = {
        initial:{
            opacity:0, 
            x:+100
        },
        visible: (custom) => ({
            opacity:1, 
            x:0,
            transition: { delay: custom/4 }
        }),
      }

      
    return(
        list.map(({img,name,link,id})=>(
            <motion.div 
                className='mainCardItems' 
                whileHover={{scale:1.02}}
                transition={{duration:.5}}
                key={id} 
                custom={id} 
                animate="visible" 
                variants={variants} 
                initial={'initial'}
            >
                <Link className='w_100 h_100 center boxColumn gap_1' style={{backgroundImage: `url(${img})`}} to={link}>
                    <h2 className='h3 txtWhite mt_2'>   
                        {name}
                    </h2>
                    <p className='btn_transparent'>
                        مشاهده صفحه
                    </p>
                </Link>
                
            </motion.div>
        ))
    )
}

const MainCarts = () => {
  return (
    <div className='mainCard'>
      <Cards />
    </div>
  )
}

export default MainCarts
