import React from 'react'
import { motion } from 'framer-motion'
import { useLocation } from 'react-router-dom'
import { BooksList } from '../../data/List'

import {Spinner, SetSpinnerTime} from '../../components/spinner/Spinner'
import Nav from '../../components/Nav'
import Header from '../../components/Header'
import Footer from '../../components/Footer'



function Book({id}) {
  return(
    <motion.div 
      key={id}
      className='ABPBox boxColumn center gap_2'
      initial={{opacity: 0}}
      animate={{opacity: 1}}
      transition={{duration:1}}
    >
      <img  
          src={BooksList[id].img} 
          alt={BooksList[id].name} 
          className='imgGrayScale ABPBox_img'
      />

      <div className='boxRow space_between w_50'>
        <p className='center pt_2'>
          <a href={BooksList[id].buyLink} className='btn_transparent'>
              خرید قانونی
          </a>
        </p>

        <div className='w_75' dir='rtl'>
          <h3 className='h3 pb_2'>{BooksList[id].name}</h3>
          {/* <h3 className='h3'>{BooksList[id].eName}</h3> */}
          <p className='txtBody'>
              <span className='txtWhite'>ناشر : </span>
              <span>{BooksList[id].publisher}</span>
          </p>
          <p className='txtBody'>
              <span className='txtWhite'>تعداد صفحات : </span>
              <span>{BooksList[id].pages}</span>
          </p>
          <p className='txtBody'>
              <span className='txtWhite'>آخرین نوبت چاپ : </span>
              <span>{BooksList[id].lastPublish}</span>
          </p>
          <p className='txtBody'>
              <span className='txtWhite'>قیمت :</span>
              <span>{BooksList[id].price}</span>
              <span> تومان </span>
          </p>
          <p className='center pt_2'>
          </p>
        </div>
      </div>
    </motion.div>
  )
}

const AboutBook = () => {
  const location = useLocation()
  let id = location.state.id;

  return (
    <>
      <Nav />
      <Header name={'کتاب '+BooksList[id].name}/>
      {
          SetSpinnerTime() ?
            <>
              <Book id={id}/>
              <Footer />
            </>
          :
            <Spinner />
        }
    </>
    
  )
}


export default AboutBook
